<template>
  <!--登陆界面--------------------------------------------------------------------------------------------- -->
  <div class="div1" >
    <!-- <img src="../../static/logo.png" alt="图片被销毁了哦" height="200" width="200" class="image1"> -->
    <div class="buttonlogin">
        <van-form @submit="subPass()" >
          <van-cell-group inset>
            <van-field v-model="login.username" label="用户名" placeholder="请输入用户名" />
            <van-field v-model="login.password" type="password" label="密码" placeholder="请输入密码" />
            <van-radio-group v-model="login.userlevel" direction="horizontal" class="select">
            <!-- <van-radio name="3">管理员</van-radio> -->
            <!-- <van-radio name="2">老师</van-radio> -->
          </van-radio-group>
          </van-cell-group>
          <div class="submitButton">
            <div @click="subPass()" class="commonButton">登录</div>
          </div>
          <!-- <img src="../../static/loginButton.png" class="submitButton" @click="subPass()"> -->
        </van-form>
    </div>
  </div>
</template>

<script>
  import { reactive, ref } from 'vue';
  import { useRouter } from "vue-router";
  import { Toast } from 'vant';

  const axios = require('axios');

  export default {
    setup(){
      const router = useRouter();
      const flag=ref(false);//这里需要改为false才能正常判断用户名密码是否正确
      const login=reactive({
        username:'',
        password:'',
        userlevel:'3',
      })

      function subPass(){
        // console.log(login.username);
        // console.log(login.password);
        //判空
        if(login.username==""||login.password=="")
        {
          Toast('请填写信息！');
          return;
        }
        //axios
        axios({
          method:'get',
          url:'user/login',
          params:login}
          )
        .then(function (response) {
          if (response.data.statuscode==3) {
            flag.value=true;
            Toast('登录成功');
          }else if(response.data.statuscode==0){
            flag.value=false;
            Toast('输入错误');
          }else if(response.data.statuscode==2){
            flag.value=false;
            Toast('未找到用户');
          }else if(response.data.statuscode==1){
            flag.value=false;
            Toast('密码错误');
          }
        })
        .catch(function (error) {
            console.log("catch"+error);
            Toast('登陆失败，请检查网络');
        });
        setTimeout(() =>{//延时
          //flag.value=true;//正式上传隐藏此行
          if (flag.value==true) {
            //console.log("登录成功！");
            router.push({path:'/batches',query:{username:login.username}});//显示batches界面
            // router.push({path:'/courses',query:{batchId:1,batchname:"亚运会"}});//直接跳转至batchId=1的界面
          }
        },100);
      }


      return{
        login,
        subPass,
        flag,
      }   
    }
  }

   
</script>

<style scoped>
  .div1{
    position: relative;
  }
  .image1{
    position: absolute;
    margin-left: 50%; 
    transform: translate(-50%);
  }
  .buttonlogin{
    width: 300px;
    position: absolute;
    margin-left: 50%; 
    margin-top: 15%;
    transform: translate(-50%);
  }
  .commonButton{
    width: 100px;
    height: 25px;
    position: absolute;
    line-height: 25px;
    margin-left: 50%; 
    margin-top: 3%;
    transform: translate(-50%);
  }
  .submitButton{
    width:150px;
    height: auto;
    margin-left: 50%; 
    margin-top: 20%;
    transform: translate(-50%, -50%);
  }
  .select{
    margin-left: 40%; 
    transform: translate(-30%);
  }
</style>